import React, { useState } from 'react';

const PaymentForm = () => {
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [amount, setAmount] = useState('');
    const [paymentUrl, setPaymentUrl] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            const response = await fetch('https://cyberspacedigital.in/(apis)/phonepe.php', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    name: name,
                    phone: phone,
                    amount: amount,
                }),
            });

            if (!response.ok) {
                throw new Error('Network response was not ok');
            }

            const responseData = await response.json();
            setPaymentUrl(responseData.paymentUrl);

        } catch (error) {
            console.error('Error:', error);
            // Handle error (e.g., show error message to user)
        }
    };

    return (
        <div className='mt-20'>
            <form onSubmit={handleSubmit}>
                <label>Name:</label>
                <input type="text" value={name} onChange={(e) => setName(e.target.value)} required />

                <label>Phone Number:</label>
                <input type="text" value={phone} onChange={(e) => setPhone(e.target.value)} required />

                <label>Amount:</label>
                <input type="number" value={amount} onChange={(e) => setAmount(e.target.value)} required />

                <button type="submit">Proceed to Pay</button>
            </form>

            {paymentUrl && (
                <div>
                    <p>Click the link below to proceed with the payment:</p>
                    <a href={paymentUrl} target="_blank" rel="noopener noreferrer">{paymentUrl}</a>
                </div>
            )}
        </div>
    );
};

export default PaymentForm;
